html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #1073ba;
}

.bg-blanco {
  background-color: #FFFFFF;
}

.margen-menos-10 {
  margin-top: -10rem;
}

.pad-bot-0 {
  padding-bottom: 0 !important;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.h2-sombra1 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra1 h2::after {
  content: "";
  background-color: #1073ba;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.h2-sombra2 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra2 h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.degradado {
  color: transparent;
  background: linear-gradient(to right, #1073ba 0%, #00da55 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.6rem;
}

p {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

/* Añade a este archivo los demas archivos de esta carpeta con @forward y recuerda añadadirlos al archivo principal (app.scss) */
.header {
  background-color: #1f1e1e;
  padding: 15rem 0;
}
.header .header-imagen {
  margin: auto 0;
}
@media (min-width: 768px) {
  .header .header-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.header .header-texto {
  color: #FFFFFF;
  text-align: center;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .header .header-texto {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
    padding-top: 0;
  }
}
.header .header-texto .producto-slogan {
  font-weight: 900;
  font-size: 3rem;
  margin: 0;
}
.header .header-texto .producto-nombre {
  font-size: 6rem;
  margin: 0;
  line-height: 1;
}
@media (min-width: 768px) {
  .header .header-texto .producto-nombre {
    font-size: 10rem;
  }
}
.header .header-texto .producto-descripcion {
  margin-bottom: 0;
}
.header .header-texto .producto-precio {
  margin: 0;
}
.header .header-texto .producto-precio span {
  font-size: 6rem;
  font-weight: 900;
}

.caracteristicas {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .caracteristicas .caracteristicas-contenido {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
.caracteristicas .caracteristica {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2.5rem;
}
.caracteristicas .caracteristica:last-of-type {
  margin-bottom: 0;
}
.caracteristicas .caracteristica .caracteristica-icono {
  width: 5rem;
}
.caracteristicas .caracteristica .caracteristica-titulo {
  color: #1073ba;
  margin-top: 3rem;
  text-transform: uppercase;
  text-align: center;
}

.informacion {
  background-image: linear-gradient(to bottom, transparent 50%, #1073ba 0%), url(/build/img/imagen-mujer.jpg);
  background-position: right;
  background-repeat: repeat, no-repeat;
  background-size: 100%, 110rem;
}
@media (min-width: 768px) {
  .informacion {
    background-image: linear-gradient(to left, transparent 50%, #1073ba 0%), url(/build/img/imagen-mujer.jpg);
    background-repeat: repeat, no-repeat;
  }
}
@media (min-width: 1600px) {
  .informacion {
    background-size: 100%, 210rem;
  }
}
.informacion .informacion-contenido {
  display: grid;
  grid-template-rows: repeat(2, 40rem);
  row-gap: 5rem;
}
@media (min-width: 768px) {
  .informacion .informacion-contenido {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
    row-gap: unset;
  }
}
.informacion .informacion-texto {
  grid-row: 2/3;
  color: #FFFFFF;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.informacion .informacion-texto h2 {
  font-size: 4rem;
}
.informacion .informacion-texto p {
  font-size: 2rem;
  line-height: 2;
}

.modelos {
  padding: 5rem 0;
}
.modelos .modelos-titulo {
  text-align: center;
  font-size: 6rem;
  margin-bottom: 5rem;
}
.modelos .modelos-lista {
  display: flex;
  flex-direction: column-reverse;
}
.modelos .modelos-lista .modelo {
  background-color: #e1e1e1;
  border-radius: 15px;
  min-height: 20rem;
  background-repeat: no-repeat;
  background-position: 90% center;
  background-size: 30%;
  margin-bottom: 2rem;
  padding-left: 4rem;
  color: #1073ba;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.3s ease-in-out;
}
.modelos .modelos-lista .modelo:hover {
  transform: scale(1.1);
  background-size: 60%;
}
.modelos .modelos-lista .modelo:first-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .modelos .modelos-lista .modelo {
    margin-bottom: 0;
  }
}
.modelos .modelos-lista .modelo-nombre {
  margin-top: 2.5rem;
  font-size: 2.4rem;
}
.modelos .modelos-lista .modelo-precio {
  font-size: 4rem;
  font-weight: 900;
  line-height: 0;
}
.modelos .modelos-lista .modelo-x {
  background-image: url(/build/img/modelo-x.svg);
}
.modelos .modelos-lista .modelo-y {
  background-image: url(/build/img/modelo-y.svg);
  grid-row: 2/3;
}
.modelos .modelos-lista .modelo-z {
  background-image: url(/build/img/modelo-z.svg);
  background-color: #1073ba;
  color: #FFFFFF;
  grid-row: 1/3;
}
@media (min-width: 768px) {
  .modelos .modelos-lista .modelo-z {
    background-size: 50%;
  }
}
.modelos .modelos-lista .modelo-z .modelo-precio {
  font-size: 6rem;
}
@media (min-width: 768px) {
  .modelos .modelos-lista {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 20rem);
    gap: 3rem;
  }
}

.newsletter {
  background-image: linear-gradient(to bottom, transparent 50%, #FFFFFF 0%), url(/build/img/newsletter.jpg);
  background-repeat: no-repeat;
  background-size: 100%, 60rem;
  background-position: top center;
}
@media (min-width: 768px) {
  .newsletter {
    background-image: linear-gradient(to right, transparent 50%, #FFFFFF 0%), url(/build/img/newsletter.jpg);
    background-size: 100%, 100rem;
    background-position: center, -21rem 0rem;
    padding: 10rem 0;
  }
}
.newsletter .newsletter-contenido {
  display: grid;
  grid-template-rows: repeat(2, 30rem);
  gap: 5rem;
}
@media (min-width: 768px) {
  .newsletter .newsletter-contenido {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.newsletter .texto-newsletter {
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .newsletter .texto-newsletter {
    grid-column: 2/3;
  }
}
.newsletter .formulario input[type=text] {
  width: 100%;
  padding: 1rem;
}
.newsletter .formulario-boton {
  width: 100%;
  padding: 2rem;
  background-color: #1073ba;
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 900;
  border: none;
}

.footer p {
  padding: 3rem 0;
  text-align: center;
  color: #1073ba;
  font-size: 2.4rem;
  margin-bottom: 0;
}

